.App {
  text-align: center;
  min-height: 100vh;
}

.App-logo {
  height: 20vmin;
}

.App-header {
  min-height: 15vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 5vmin);
  color: white;
}

.App-info {
  margin-bottom: 8px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(12px);
  color: white;
}

.stream-status {
  font-size: calc(12px);
  color: white;
  margin-bottom: 5px;
}

.error-status {
  font-size: calc(12px);
  color: yellow;
  margin-bottom: 5px;
  background: black;
}

.App-footer {
  min-height: 25vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(12px);
  color: white;
}

a {
  color: #09d3ac;
  text-decoration: none;
}

.particles-js canvas {
  display: block;
  -webkit-transform: scale(1);
  -ms-transform: scale(1);
  transform: scale(1);
  opacity: 1;
  -webkit-transition: opacity 0.8s ease, -webkit-transform 1.4s ease;
  transition: opacity 0.8s ease, transform 1.4s ease;
}

.particles-js {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: -10;
  top: 0;
  left: 0;
}

.not-working {
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(12px);
  color: white;
  font-style: italic;
}

.App-social {
  margin-bottom: 15px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(40px);
  color: white;
}

.spotify-icon {
  color: #1db954;
  margin-right: 10px;
}

.spotify-icon:hover {
  cursor: pointer;
}

.twitter-icon {
  color: #55acee;
  margin-left: 10px;
  margin-right: 10px;
}

.twitter-icon:hover {
  cursor: pointer;
}

.twitch-icon {
  color: #874af6;
  margin-left: 10px;
}

.twitch-icon:hover {
  cursor: pointer;
}
